import React, { Component } from "react";

import icons from "assets/img/sprite.svg";

class AppSidebar extends Component {
  render() {
    return (
      <nav className="sidebar">
        <ul className="nav-side">
          <li className="nav-side__item nav-side__item--active">
            <a href="#" className="nav-side__link">
              <svg className="nav-side__icon">
                <use xlinkHref={icons + "#icon-home"}></use>
              </svg>
              <span>Home</span>
            </a>
          </li>
          <li className="nav-side__item">
            <a href="#" className="nav-side__link">
              <svg className="nav-side__icon">
                <use xlinkHref={icons + "#icon-aircraft"}></use>
              </svg>
              <span>Tour</span>
            </a>
          </li>
          <li className="nav-side__item">
            <a href="#" className="nav-side__link">
              <svg className="nav-side__icon">
                <use xlinkHref={icons + "#icon-key"}></use>
              </svg>
              <span>Setting</span>
            </a>
          </li>
          <li className="nav-side__item">
            <a href="#" className="nav-side__link">
              <svg className="nav-side__icon">
                <use xlinkHref={icons + "#icon-map"}></use>
              </svg>
              <span>Map</span>
            </a>
          </li>
        </ul>
        <div className="legal">&copy; 2019. All Rights reserved</div>
      </nav>
    );
  }
}

export default AppSidebar;
