import React, { Component } from "react";
import logo from "assets/img/sprite.svg";
import avtar from "assets/img/user.jpg";

class AppHeader extends Component {
  render() {
    return (
      <header className="header">
        <svg className="logo">
          <use xlinkHref={logo + "#icon-loop2"}></use>
        </svg>
        <form action="#" className="search">
          <input
            type="text"
            className="search__input"
            placeholder="  Search Transactions..."
          />
          <button className="search__button">
            <svg className="search__icon">
              <use xlinkHref={logo + "#icon-magnifying-glass"}></use>
            </svg>
          </button>
        </form>

        <nav className="user-nav">
          <div className="user-nav__icon-box">
            <svg className="user-nav__icon">
              <use xlinkHref={logo + "#icon-bookmark"}></use>
            </svg>
            <span className="user-nav__notifications">7</span>
          </div>
          <div className="user-nav__icon-box">
            <svg className="user-nav__icon">
              <use xlinkHref={logo + "#icon-chat"}></use>
            </svg>
            <span className="user-nav__notifications">13</span>
          </div>
          <div className="user-nav__user">
            <img
              src={avtar}
              className="user-nav__user-photo"
              alt="user avtar"
            />
            <span className="user-nav__user-name">Ashutosh</span>
          </div>
        </nav>
      </header>
    );
  }
}
export default AppHeader;

